.main-container {
  display: flex;
  flex-direction: row;
  justify-self: start;

  .card {
    background: rgba(105, 105, 105, 0.4);
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
    max-width: 300px;
    border-radius: 10px;
    margin-right: 20px;

    h2 {
      margin-top: 0;
      text-align: center;
      border-radius: 10px;
      background: rgba(160, 160, 160, 0.4);
    }

    .card-body {
      display: block;
      justify-content: space-between;
      margin-left: 30px;

      h3 {
        color: orange;
        padding: 0px;
        margin: 0px;
      }

      p {
        padding: 0;
        margin: 0;
      }

      hr {
        width: 95%;
        margin-left: -10px;
        padding: 0;
        margin-top: 0;
        margin-bottom: 5px;
      }

      button {
        color: white;
        background-color: red;
        border: none;
        border-radius: 10px;
        padding: 10px 25px;
        margin: 15px 145px;
        font-size: 14px;

        &:hover {
          background-color: rgb(200, 5, 5);
          transition: 0.2s ease-in-out;
          cursor: pointer;
        }
      }
    }
  }
}
